import React, { useState, useEffect, useRef } from 'react';
import { vocabularyData } from '../data/vocabularyData';
import '../styles/VocabularyTab.css';

function VocabularyTab({ vocabularySettings, setVocabularySettings }) {
  const [mode, setMode] = useState(() => {
    return localStorage.getItem('vocabularyMode') || 'study';
  }); // study or known
  const level = vocabularySettings?.level || 'lesson';
  const category = vocabularySettings?.category || 'all';
  const [knownWords, setKnownWords] = useState(() => {
    const saved = localStorage.getItem('knownWords');
    return saved ? JSON.parse(saved) : [];
  });
  const [wordStates, setWordStates] = useState({});
  const [allWords, setAllWords] = useState([]);
  const [filteredWords, setFilteredWords] = useState([]);
  const [showAllFurigana, setShowAllFurigana] = useState(false);
  const [showAllMeaning, setShowAllMeaning] = useState(false);
  const [showAllWords] = useState(false);
  const [currentPage, setCurrentPage] = useState(() => {
    const saved = localStorage.getItem('vocabularyCurrentPage');
    return saved ? parseInt(saved) : 0;
  });
  const wordsPerPage = 50;

  // Initialize words
  useEffect(() => {
    const vocabulary = vocabularyData[level] || vocabularyData.lesson;
    const words = [];
    let globalIndex = 0;
    
    // Process categories in original order
    Object.entries(vocabulary).forEach(([cat, items]) => {
      items.forEach((item, categoryIndex) => {
        words.push({
          ...item,
          category: cat,
          level: level,
          id: `${level}-${cat}-${item.jp}`,
          levelIndex: globalIndex++,
          categoryIndex: categoryIndex,
          originalOrder: globalIndex
        });
      });
    });
    
    setAllWords(words);
    setShowAllFurigana(false);
    setShowAllMeaning(false);
    setWordStates({});
  }, [level]);

  // Filter words based on category and known status
  useEffect(() => {
    let filtered = [...allWords];
    
    // Filter by category
    if (category !== 'all') {
      filtered = filtered.filter(word => word.category === category);
    }
    
    // Filter by known status
    if (mode === 'study') {
      // In study mode, hide known words
      if (!showAllWords) {
        filtered = filtered.filter(word => !knownWords.includes(word.id));
      }
    } else {
      // In known mode, show all known words across all levels
      const allKnownWords = [];
      let globalIndexCounter = 0;
      Object.keys(vocabularyData).forEach(lvl => {
        const levelVocab = vocabularyData[lvl];
        Object.entries(levelVocab).forEach(([cat, items]) => {
          items.forEach((item, categoryIndex) => {
            const wordId = `${lvl}-${cat}-${item.jp}`;
            if (knownWords.includes(wordId)) {
              allKnownWords.push({
                ...item,
                category: cat,
                level: lvl,
                id: wordId,
                levelIndex: globalIndexCounter,
                categoryIndex: categoryIndex,
                originalOrder: globalIndexCounter
              });
            }
            globalIndexCounter++;
          });
        });
      });
      filtered = allKnownWords;
    }
    
    setFilteredWords(filtered);
  }, [allWords, category, mode, knownWords, showAllWords]);

  // Pagination logic
  const paginatedWords = filteredWords.slice(
    currentPage * wordsPerPage,
    (currentPage + 1) * wordsPerPage
  );
  const totalPages = Math.ceil(filteredWords.length / wordsPerPage);

  // Handle page navigation
  const goToNextPage = () => {
    if (currentPage < totalPages - 1) {
      const newPage = currentPage + 1;
      setCurrentPage(newPage);
      localStorage.setItem('vocabularyCurrentPage', newPage.toString());
      // Update URL with page info
      window.history.pushState({ 
        tab: 'vocabulary', 
        level: level, 
        category: category, 
        page: newPage 
      }, '', `#vocabulary-${level}-${category}-page${newPage}`);
      setTimeout(() => {
        const vocabList = document.getElementById('vocab-list');
        if (vocabList) {
          vocabList.scrollTop = 0;
        }
        window.scrollTo(0, 0);
      }, 0);
    }
  };

  const goToPrevPage = () => {
    if (currentPage > 0) {
      const newPage = currentPage - 1;
      setCurrentPage(newPage);
      localStorage.setItem('vocabularyCurrentPage', newPage.toString());
      // Update URL with page info
      window.history.pushState({ 
        tab: 'vocabulary', 
        level: level, 
        category: category, 
        page: newPage 
      }, '', `#vocabulary-${level}-${category}-page${newPage}`);
      setTimeout(() => {
        const vocabList = document.getElementById('vocab-list');
        if (vocabList) {
          vocabList.scrollTop = 0;
        }
        window.scrollTo(0, 0);
      }, 0);
    }
  };

  // Track previous filter values to detect actual changes (not initial load)
  const prevFiltersRef = useRef({ category, level, mode });
  const [isFirstRender, setIsFirstRender] = useState(true);
  
  useEffect(() => {
    // Only reset page if filters actually changed (not on initial load/refresh)
    if (!isFirstRender) {
      const prevFilters = prevFiltersRef.current;
      if (prevFilters.category !== category || prevFilters.level !== level || prevFilters.mode !== mode) {
        setCurrentPage(0);
        localStorage.setItem('vocabularyCurrentPage', '0');
        // Clear word states when switching categories/levels/modes
        setWordStates({});
        setShowAllFurigana(false);
        setShowAllMeaning(false);
      }
    }
    
    // Update previous values and mark as not first render
    prevFiltersRef.current = { category, level, mode };
    setIsFirstRender(false);
    
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [category, level, mode]);

  // Update settings when level/category changes from menu
  useEffect(() => {
    if (setVocabularySettings && (vocabularySettings?.level !== level || vocabularySettings?.category !== category)) {
      setVocabularySettings({
        level: level,
        category: category
      });
    }
  }, [level, category, setVocabularySettings, vocabularySettings]);

  // Listen for page changes from back button
  useEffect(() => {
    const handlePageChange = (event) => {
      const newPage = event.detail;
      setCurrentPage(newPage);
      setTimeout(() => {
        const vocabList = document.getElementById('vocab-list');
        const knownList = document.getElementById('known-list');
        if (mode === 'study' && vocabList) {
          vocabList.scrollTop = 0;
        } else if (mode === 'known' && knownList) {
          knownList.scrollTop = 0;
        }
        window.scrollTo(0, 0);
      }, 0);
    };

    window.addEventListener('vocabularyPageChange', handlePageChange);
    return () => {
      window.removeEventListener('vocabularyPageChange', handlePageChange);
    };
  }, [mode]);

  // Toggle word state
  const toggleWordState = (wordId, state) => {
    if (state === 'reset') {
      // Toggle visibility: if both are hidden, show both. Otherwise, hide both.
      const currentState = wordStates[wordId];
      const currentShowFurigana = currentState?.showFurigana ?? showAllFurigana;
      const currentShowMeaning = currentState?.showMeaning ?? showAllMeaning;
      const shouldShow = !currentShowFurigana && !currentShowMeaning;
      
      setWordStates(prev => ({
        ...prev,
        [wordId]: {
          showFurigana: shouldShow,
          showMeaning: shouldShow
        }
      }));
    } else {
      setWordStates(prev => {
        const currentState = prev[wordId];
        const currentValue = currentState?.[state] ?? (state === 'showFurigana' ? showAllFurigana : showAllMeaning);
        return {
          ...prev,
          [wordId]: {
            ...currentState,
            [state]: !currentValue
          }
        };
      });
    }
  };

  
  // Toggle all furigana and meanings
  const toggleAllDisplay = () => {
    const shouldShow = !showAllFurigana && !showAllMeaning;
    setShowAllFurigana(shouldShow);
    setShowAllMeaning(shouldShow);
    
    const newStates = {};
    paginatedWords.forEach(word => {
      newStates[word.id] = {
        showFurigana: shouldShow,
        showMeaning: shouldShow
      };
    });
    setWordStates(newStates);
  };

  // Mark word as known
  const markAsKnown = (wordId) => {
    const newKnownWords = [...knownWords, wordId];
    setKnownWords(newKnownWords);
    localStorage.setItem('knownWords', JSON.stringify(newKnownWords));
  };

  // Restore word from known
  const restoreWord = (wordId) => {
    const newKnownWords = knownWords.filter(id => id !== wordId);
    setKnownWords(newKnownWords);
    localStorage.setItem('knownWords', JSON.stringify(newKnownWords));
    
    // If this was the last word on the current page, go to the previous page
    const currentPageWords = filteredWords.slice(
      currentPage * wordsPerPage,
      (currentPage + 1) * wordsPerPage
    );
    
    if (currentPageWords.length === 1 && currentPage > 0) {
      const newPage = currentPage - 1;
      setCurrentPage(newPage);
      localStorage.setItem('vocabularyCurrentPage', newPage.toString());
    }
  };


  // Toggle all furigana
  const toggleAllFurigana = () => {
    const newShowFurigana = !showAllFurigana;
    setShowAllFurigana(newShowFurigana);
    const newStates = {};
    paginatedWords.forEach(word => {
      newStates[word.id] = {
        ...wordStates[word.id],
        showFurigana: newShowFurigana
      };
    });
    setWordStates(newStates);
  };

  // Toggle all meanings
  const toggleAllMeaning = () => {
    const newShowMeaning = !showAllMeaning;
    setShowAllMeaning(newShowMeaning);
    const newStates = {};
    paginatedWords.forEach(word => {
      newStates[word.id] = {
        ...wordStates[word.id],
        showMeaning: newShowMeaning
      };
    });
    setWordStates(newStates);
  };



  // Restore all words
  const restoreAll = () => {
    setKnownWords([]);
    localStorage.removeItem('knownWords');
    // Reset current page and scroll to top
    setCurrentPage(0);
    // Force re-render by clearing filteredWords
    setTimeout(() => {
      const knownList = document.getElementById('known-list');
      if (knownList) {
        knownList.scrollTop = 0;
      }
      window.scrollTo(0, 0);
    }, 0);
  };


  return (
    <div className="vocabulary-tab">
      {/* Fixed Header Container */}
      <div className="vocabulary-fixed-header">
        {/* Mode Selector */}
        <div className="mode-selector">
        <button 
          className={`mode-btn ${mode === 'study' ? 'active' : ''}`}
          onClick={() => {
            if (mode !== 'study') {
              setCurrentPage(0);
              localStorage.setItem('vocabularyCurrentPage', '0');
              setMode('study');
              localStorage.setItem('vocabularyMode', 'study');
              setTimeout(() => {
                const vocabList = document.getElementById('vocab-list');
                if (vocabList) {
                  vocabList.scrollTop = 0;
                }
                window.scrollTo(0, 0);
              }, 0);
            }
          }}
        >
          학습할 단어 ({allWords.length - knownWords.length})
        </button>
        <button 
          className={`mode-btn ${mode === 'known' ? 'active' : ''}`}
          onClick={() => {
            if (mode !== 'known') {
              setCurrentPage(0);
              localStorage.setItem('vocabularyCurrentPage', '0');
              setMode('known');
              localStorage.setItem('vocabularyMode', 'known');
              setTimeout(() => {
                const knownList = document.getElementById('known-list');
                if (knownList) {
                  knownList.scrollTop = 0;
                }
                window.scrollTo(0, 0);
              }, 0);
            }
          }}
        >
          완료한 단어 ({knownWords.length})
        </button>
      </div>
      </div>

      {/* Content Area */}
      <div className="vocabulary-content">
      {mode === 'study' ? (
        <div id="study-mode">
          <div className="study-mode-sticky-header">
          <div className="fixed-controls-container">
            <div className="action-controls">
              <button className="compact-action-btn" onClick={toggleAllFurigana}>
                전체 히라가나
              </button>
              <button className="compact-action-btn" onClick={toggleAllMeaning}>
                전체 뜻
              </button>
              <button className="compact-action-btn" onClick={toggleAllDisplay}>
                👁 {(showAllFurigana && showAllMeaning) ? '전체 숨기기' : '전체 보기'}
              </button>
            </div>

            <div className="page-navigation-container">
              <button 
                className="nav-btn-aligned" 
                onClick={goToPrevPage}
                disabled={currentPage === 0 || totalPages <= 1}
              >
                ← 이전
              </button>
              <span className="page-display-center">
                {currentPage + 1} / {totalPages || 1}
              </span>
              <button 
                className="nav-btn-aligned" 
                onClick={goToNextPage}
                disabled={currentPage === totalPages - 1 || totalPages <= 1}
              >
                다음 →
              </button>
            </div>
          </div>
          </div>

          {/* Word List */}
          <div 
            className="interactive-vocab-list" 
            id="vocab-list"
          >
            {filteredWords.length === 0 ? (
              <div className="empty-message">모든 단어를 학습했습니다! 🎉</div>
            ) : (
              paginatedWords.sort((a, b) => {
                // If filtering by category, use categoryIndex; otherwise use levelIndex
                if (category !== 'all') {
                  return a.categoryIndex - b.categoryIndex;
                } else {
                  return a.levelIndex - b.levelIndex;
                }
              }).map((word, index) => {
                // Use word's unique level index instead of page-based index
                const displayIndex = word.levelIndex + 1;
                return (
                <div key={word.id} className="interactive-word-card">
                  <span style={{position: 'absolute', top: '10px', left: '15px', color: 'rgba(255,255,255,0.7)', fontSize: '14px', fontWeight: '600'}}>
                    #{displayIndex}
                  </span>
                  <div className="word-column">
                    <div className="japanese-word">
                      {((wordStates[word.id]?.showFurigana ?? showAllFurigana)) && word.furigana ? (
                        <ruby>{word.jp}<rt>{word.furigana}</rt></ruby>
                      ) : (
                        word.jp
                      )}
                    </div>
                    {(wordStates[word.id]?.showMeaning ?? showAllMeaning) && (
                      <div className="word-meaning">{word.kr}</div>
                    )}
                  </div>
                  <div className="action-buttons">
                    <button 
                      className={`action-btn-small ${(wordStates[word.id]?.showFurigana ?? showAllFurigana) ? 'active' : ''}`}
                      onClick={() => toggleWordState(word.id, 'showFurigana')}
                      disabled={!word.furigana}
                    >
                      あ
                    </button>
                    <button 
                      className={`action-btn-small ${(wordStates[word.id]?.showMeaning ?? showAllMeaning) ? 'active' : ''}`}
                      onClick={() => toggleWordState(word.id, 'showMeaning')}
                    >
                      뜻
                    </button>
                    <button 
                      className="action-btn-small btn-reset"
                      onClick={() => toggleWordState(word.id, 'reset')}
                      title="리셋"
                    >
                      👁
                    </button>
                    <button 
                      className="action-btn-small btn-know"
                      onClick={() => markAsKnown(word.id)}
                    >
                      암기완료
                    </button>
                  </div>
                </div>
                );
              })
            )}
          </div>
        </div>
      ) : (
        <div id="known-mode">
          <div className="known-mode-sticky-header">
          <div className="known-mode-fixed-header">
            <div className="compact-controls">
              <button className="compact-action-btn" onClick={restoreAll}>
                모든 단어 되돌리기
              </button>
            </div>
          </div>
          </div>
          <div className="known-words-grid" id="known-list">
            {filteredWords.length === 0 ? (
              <div className="empty-message">아직 완료한 단어가 없습니다.</div>
            ) : (
              filteredWords.map(word => (
                <div 
                  key={word.id} 
                  className="known-word-card"
                  onClick={() => restoreWord(word.id)}
                >
                  <div className="japanese-text">{word.jp}</div>
                  <div className="korean-text">{word.kr}</div>
                  <div className="restore-text">✓ 완료 - 탭하여 복원</div>
                </div>
              ))
            )}
          </div>
        </div>
      )}
      </div>
    </div>
  );
}

export default VocabularyTab;