import React, { useState } from 'react';
import './styles/App.css';
import VocabularyTab from './components/VocabularyTab';
import WordQuizTab from './components/WordQuizTab';
import SentenceQuizTab from './components/SentenceQuizTab';
import ConversationTab from './components/ConversationTab';

function App() {
  const [activeTab, setActiveTab] = useState(() => {
    // Check URL first, then localStorage
    const hash = window.location.hash;
    if (hash) {
      const tab = hash.substring(1).split('-')[0];
      if (['vocabulary', 'wordQuiz', 'sentenceQuiz', 'conversation'].includes(tab)) {
        return tab;
      }
    }
    return localStorage.getItem('activeTab') || 'vocabulary';
  });
  const [showMenu, setShowMenu] = useState(false);
  const [showVocabMenu, setShowVocabMenu] = useState(false);
  const [vocabularySettings, setVocabularySettings] = useState(() => {
    const saved = localStorage.getItem('vocabularySettings');
    return saved ? JSON.parse(saved) : { level: 'lesson', category: 'all' };
  });
  const [conversationState, setConversationState] = useState({
    selectedLesson: null
  });

  const toggleMenu = () => {
    if (showMenu) {
      // When closing menu, reset vocab submenu state
      setShowVocabMenu(false);
    }
    setShowMenu(!showMenu);
  };

  const switchTab = (tab) => {
    setActiveTab(tab);
    localStorage.setItem('activeTab', tab);
    setShowMenu(false);
    setShowVocabMenu(false);
    // Change URL to reflect current tab
    window.history.pushState({ tab }, '', `#${tab}`);
  };

  // Close menu when clicking outside
  React.useEffect(() => {
    const handleClickOutside = (event) => {
      if (showMenu && !event.target.closest('.menu-container')) {
        // Prevent any button functionality when clicking empty space
        event.preventDefault();
        event.stopPropagation();
        setShowMenu(false);
        setShowVocabMenu(false);
      }
    };

    document.addEventListener('click', handleClickOutside, true);
    return () => {
      document.removeEventListener('click', handleClickOutside, true);
    };
  }, [showMenu]);

  // Handle browser back button navigation
  React.useEffect(() => {
    const handlePopState = (event) => {
      const state = event.state;
      const hash = window.location.hash;
      
      // If we have state from our pushState, use it
      if (state && state.tab) {
        setActiveTab(state.tab);
        localStorage.setItem('activeTab', state.tab);
        
        if (state.level) {
          const newSettings = {...vocabularySettings, level: state.level};
          setVocabularySettings(newSettings);
          localStorage.setItem('vocabularySettings', JSON.stringify(newSettings));
        }
        
        if (state.category) {
          const newSettings = {...vocabularySettings, category: state.category};
          setVocabularySettings(newSettings);
          localStorage.setItem('vocabularySettings', JSON.stringify(newSettings));
        }
        
        if (state.page !== undefined) {
          localStorage.setItem('vocabularyCurrentPage', state.page.toString());
          window.dispatchEvent(new CustomEvent('vocabularyPageChange', { detail: state.page }));
        }
        
        return;
      }
      
      // Handle hash-based navigation
      if (hash) {
        const parts = hash.substring(1).split('-');
        const tab = parts[0];
        
        if (['vocabulary', 'wordQuiz', 'sentenceQuiz', 'conversation'].includes(tab)) {
          setActiveTab(tab);
          localStorage.setItem('activeTab', tab);
          return;
        }
      }
      
      // If no valid navigation target, go to vocabulary
      setActiveTab('vocabulary');
      localStorage.setItem('activeTab', 'vocabulary');
      window.history.pushState({ tab: 'vocabulary' }, '', '#vocabulary');
    };

    // Initialize with current tab
    const currentHash = window.location.hash || '#vocabulary';
    if (!currentHash.includes('#')) {
      window.history.replaceState({ tab: activeTab }, '', `#${activeTab}`);
    }
    
    window.addEventListener('popstate', handlePopState);
    
    return () => {
      window.removeEventListener('popstate', handlePopState);
    };
  }, [activeTab, showMenu, showVocabMenu, conversationState]);

  // Push state when navigation state changes
  React.useEffect(() => {
    // Add a history entry whenever we navigate to track state changes
    window.history.pushState(null, '', window.location.pathname);
  }, [activeTab, showMenu, showVocabMenu, conversationState]);

  return (
    <div className="app-container">
      <div className="main-content">
        {/* Header with Menu */}
        <div className="tab-header">
          <h2 className="tab-title">
            {activeTab === 'vocabulary' && '스마트 반복 단어장'}
            {activeTab === 'wordQuiz' && '단어 퀴즈'}
            {activeTab === 'sentenceQuiz' && '문장 퀴즈'}
            {activeTab === 'conversation' && '수업 대화문'}
          </h2>
          <div className="menu-container">
            <button className="menu-btn" onClick={toggleMenu}>
              메뉴
            </button>
            {showMenu && (
              <div className="menu-dropdown show">
                <div 
                  className={`menu-item vocab-toggle ${activeTab === 'vocabulary' ? 'active' : ''}`}
                  onClick={(e) => {
                    e.stopPropagation();
                    if (activeTab === 'vocabulary') {
                      setShowVocabMenu(!showVocabMenu);
                    } else {
                      switchTab('vocabulary');
                    }
                  }}
                >
                  <span>단어장</span>
                  {activeTab === 'vocabulary' && (
                    <span className={`menu-arrow ${showVocabMenu ? 'expanded' : ''}`}>▼</span>
                  )}
                </div>
                {activeTab === 'vocabulary' && showVocabMenu && (
                  <div className="vocab-submenu">
                    <div className="menu-separator"></div>
                    <div className="submenu-section">
                      <div className="submenu-title">레벨</div>
                      <div className="submenu-buttons">
                        {[
                          { value: 'lesson', label: '수업' },
                          { value: 'n5', label: 'N5' },
                          { value: 'n4', label: 'N4' },
                          { value: 'n3', label: 'N3' }
                        ].map(level => (
                          <button
                            key={level.value}
                            className={`submenu-btn ${vocabularySettings.level === level.value ? 'active' : ''}`}
                            onClick={(e) => {
                              e.stopPropagation();
                              const newSettings = {...vocabularySettings, level: level.value};
                              setVocabularySettings(newSettings);
                              localStorage.setItem('vocabularySettings', JSON.stringify(newSettings));
                              window.history.pushState({ tab: 'vocabulary', level: level.value }, '', `#vocabulary-${level.value}`);
                            }}
                          >
                            {level.label}
                          </button>
                        ))}
                      </div>
                    </div>
                    <div className="submenu-section">
                      <div className="submenu-title">카테고리</div>
                      <select 
                        className="submenu-select"
                        value={vocabularySettings.category}
                        onChange={(e) => {
                          const newSettings = {...vocabularySettings, category: e.target.value};
                          setVocabularySettings(newSettings);
                          localStorage.setItem('vocabularySettings', JSON.stringify(newSettings));
                          window.history.pushState({ tab: 'vocabulary', category: e.target.value }, '', `#vocabulary-${vocabularySettings.level}-${e.target.value}`);
                        }}
                        onClick={(e) => e.stopPropagation()}
                      >
                        <option value="all">전체 보기</option>
                        <option value="동사">동사</option>
                        <option value="명사">명사</option>
                        <option value="い형용사">い형용사</option>
                        <option value="な형용사">な형용사</option>
                        <option value="부사기타">부사기타</option>
                      </select>
                    </div>
                  </div>
                )}
                <div 
                  className={`menu-item ${activeTab === 'wordQuiz' ? 'active' : ''}`}
                  onClick={() => switchTab('wordQuiz')}
                >
                  단어 퀴즈
                </div>
                <div 
                  className={`menu-item ${activeTab === 'sentenceQuiz' ? 'active' : ''}`}
                  onClick={() => switchTab('sentenceQuiz')}
                >
                  문장 퀴즈
                </div>
                <div 
                  className={`menu-item ${activeTab === 'conversation' ? 'active' : ''}`}
                  onClick={() => switchTab('conversation')}
                >
                  수업 대화문
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Tab Content */}
        <div className="tab-container">
          {activeTab === 'vocabulary' && <VocabularyTab vocabularySettings={vocabularySettings} setVocabularySettings={setVocabularySettings} />}
          {activeTab === 'wordQuiz' && <WordQuizTab />}
          {activeTab === 'sentenceQuiz' && <SentenceQuizTab />}
          {activeTab === 'conversation' && <ConversationTab conversationState={conversationState} setConversationState={setConversationState} />}
        </div>
      </div>
    </div>
  );
}

export default App;